@font-face {
  font-family: "Lato";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src:
    local("Lato Light"),
    local("Lato-Light"),
    url("/fonts/Lato-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    local("Lato"),
    local("Lato-Regular"),
    url("/fonts/Lato-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Lato";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src:
    local("Lato Semibold"),
    local("Lato-Semibold"),
    url("/fonts/Lato-Semibold.woff2") format("woff2");
}

@font-face {
  font-family: "Gilroy";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    local("Gilroy"),
    local("Gilroy-Regular"),
    url("/fonts/Gilroy-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Giorgio Sans LCG";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src:
    local("Giorgio Sans LCG Bold"),
    local("Giorgio-Sans-LCG-Bold"),
    url("/fonts/Giorgio-Sans-LCG-Bold.woff2") format("woff2");
}
